import React, { useState } from 'react';

import './ImageWallPaperCfg.scss';

export default () => {
    const [ssid, setSSID] = useState(localStorage.getItem('ssid') || '');
    const [pwd, setPwd] = useState(localStorage.getItem('password') || '');
    const [wakeTime, setWakeTime] = useState(60);
    const [url, setUrl] = useState('');
    const [invalidState, setInvalidState] = useState(false);
    const [pwdVisible, setPwdVisible] = useState(false);
    const togglePwdVisible = () => setPwdVisible(!pwdVisible);
    const saveSSID = ev => {
        setSSID(ev.target.value);
        localStorage.setItem('ssid', ev.target.value);
    }
    const savePwd = ev => {
        setPwd(ev.target.value);
        localStorage.setItem('password', ev.target.value);
    }
    const handleUpdateUrl = ev => {
        if(ev.target.value === '') {
            setInvalidState(false);
        }
        setUrl(ev.target.value);
    }
    const handleImageLoaded = ev => {
        setInvalidState(false);
        document.getElementById('__preview__imagewp').style.display = 'inline-block';
    }
    const handlePreviewError = err => {
        setInvalidState(true);
        document.getElementById('__preview__imagewp').style.display = 'none';
    }
    const handleBlur = ev => {
        if(ev.target.value === '') {
            setWakeTime(30);
        }
        if(ev.target.value > 172800) {
            setWakeTime(172800);
        }
        if(ev.target.value < 60) {
            setWakeTime(60);
        }
    }

    return (
        <>
            <table className="wrapper-timercambox">
                <tbody>
                    <tr className="wifibox-item">
                        <td className="field-name">SSID:</td>
                        <td>
                            <input id="imagewp__config__ssid" type="text" value={ssid} onChange={saveSSID} />
                        </td>
                    </tr>
                    <tr className="wifibox-item password">
                        <td className="field-name">Password:</td>
                        <td>
                            <input id="imagewp__config__password" type={!pwdVisible?"password":"text"} value={pwd} onChange={savePwd} />
                            <img
                                className="btn-eye"
                                src={!pwdVisible ? "./assets/images/show.png" : "./assets/images/hide.png"}
                                onClick={togglePwdVisible}
                                alt=""
                            />
                        </td>
                    </tr>
                    <tr className="wifibox-item">
                        <td className="field-name">
                            <p>Image Url:</p>
                            <p>(JPG Only)</p>
                        </td>
                        <td>
                            <input id="imagewp__config__url" type="text" value={url} onChange={ev => handleUpdateUrl(ev)} />
                            {
                                url !== ''
                                ?
                                <img id="__preview__imagewp" height="200" style={{'marginTop': 10}} src={url}
                                    onLoad={ev => handleImageLoaded(ev)}
                                    onError={err => handlePreviewError(err)}
                                />
                                : null
                            }
                            {
                                invalidState
                                ? <p style={{fontSize: 12, color: 'red', marginTop: 6}}>Invalid Image Url</p>
                                : null
                            }
                        </td>
                    </tr>
                    <tr className="wifibox-item">
                        <td className="field-name">Wake Time:</td>
                        <td>
                            <input
                                id="imagewp__config__waketime"
                                type="number"
                                value={wakeTime}
                                onChange={ev => setWakeTime(ev.target.value)}
                                onBlur={handleBlur}
                            />
                            <span>sec (60 ~ 172800)</span>
                        </td>
                    </tr>
                </tbody>
            </table>
        </>
    )
}
