import React, { useState } from 'react';
import './LoginForm.scss';

export default props => {
    let [email, setEmail] = useState(localStorage.getItem('m5sso_email') || '');
    let [pwd, setPwd] = useState(localStorage.getItem('m5sso_password') || '');
    let [loading, setLoading] = useState(false);

    let handleLogin = () => {
        if(loading) return;
        setLoading(true);
        props.onLogin(email, pwd, () => {
            setLoading(false);
            window.swal({
                text: `Login failed, check your email or password.`,
                icon: 'error',
            })
        });
    }

    let handleRegister = ev => {
        props.gotoRegister();
    }

    let handleReset = ev => {
        props.gotoReset();
    }

    let handleCancel = () => {
        window.swal.close();
    }

    return (
        <div className="login--form">
            <img className="logo" src="assets/images/logo.png" alt="logo" />
            <div className="item">
                <span>Email:</span>
                <input value={email} onChange={ev => setEmail(ev.target.value)} />
            </div>
            <div className="item">
                <span>Password:</span>
                <input value={pwd} type="password" onChange={ev => setPwd(ev.target.value)} />
            </div>
            <div className="item">
                <a className="link" onClick={ev => handleRegister(ev)}>
                    Create a new account
                </a>
            </div>
            <div className="item">
                <a className="link" onClick={ev => handleReset(ev)}>
                    Forget password
                </a>
            </div>
            <div className="controller">
                <button className="btn-cancel" onClick={handleCancel}>
                    <span>Cancel</span>
                </button>
                <button className={loading?'loading':''} onClick={handleLogin}>
                    {
                        loading
                        ? <i className="iconfont icon-spin spin"></i>
                        : null
                    }
                    <span>Login</span>
                </button>
            </div>
        </div>
    )
}
