import { FirmwareActionTypes } from "../actions/firmware.action";
import { DownloadStatus } from "../constants";

const initialState = {
    list: {
        core: [],
        core2: [],
        stickc: [],
        atom: [],
        stickv: [],
        timercam: [],
        coreink: [],
        paper: [],
        stamp: []
    },
    raw: {
        core: [],
        core2: [],
        stickc: [],
        atom: [],
        stickv: [],
        timercam: [],
        coreink: [],
        paper: [],
        stamp: []
    },
    selected: 'core',
    keyword: ''
}

export default function reducer(state = initialState, action) {
    switch(action.type) {
        case FirmwareActionTypes.GET_FIRMWARE:
            return {
                ...state,
                list: action.payload,
                raw: action.payload
            }
        case FirmwareActionTypes.UPDATE_FIRMWARES:
            var newList = JSON.parse(JSON.stringify(action.payload));
            return {
                ...state,
                list: newList
            }
        case FirmwareActionTypes.SELECT_FIRMWARE_TYPE:
            return {
                ...state,
                selected: action.payload
            }
        case FirmwareActionTypes.UPDATE_DOWNLOAD_STATUS:
            var newState = JSON.parse(JSON.stringify(state));
            var newList = newState.list[action.payload.type];
            newList.forEach(firmware => {
                if(firmware.name === action.payload.name) {
                    var item = firmware.versions.find(v => v.version === action.payload.version);
                    item.downloadStatus = action.payload.downloadStatus;
                    item.progress = action.payload.progress;
                    if(action.payload.downloadStatus === DownloadStatus.END) {
                        item.exist = true;
                    }
                }
            });
            return {
                ...state,
                ...newState
            }
        case FirmwareActionTypes.SEARCH_FIRMWARE:
            var newState = JSON.parse(JSON.stringify(state));
            if(action.payload === '') {
                return {
                    ...newState,
                    list: newState.raw
                }
            }
            let currentList = {}
            for(let device in newState.list) {
                currentList[device] = newState.raw[device].filter(f => f.name.toUpperCase().includes(action.payload.toUpperCase()));
            }
            return {
                ...newState,
                list: currentList
            }
        default:
            return state;
    }
}