import React from 'react';
import Qrcode from 'qrcode.react';

import './QRCode.scss';

const URL = 'http://api.m5stack.com:5003/timer-cam/image?tok='

export default props => {
    return (
        <div className="qrcode-modal">
            <h3>
                <p>Token</p>
                <p>{props.token}</p>
            </h3>
            <Qrcode value={URL + props.token} size={200} fgColor="#000000" />
            <h3>Scan QRCode to browser your image.</h3>
        </div>
    )
}
