import React, { useEffect, useRef } from 'react';
import cs from 'classnames';

import './DeviceList.scss';

function alias(name) {
    if(name === 'stickv') {
        return 'stickv & unitv';
    }
    else if(name === 'core2') {
        return 'core2 & tough';
    }
    return name;
}

function DeviceList(props) {
    const { owns, selected, setSelected } = props;
    const treeRef = useRef();
    const topArrowRef = useRef();
    const bottomArrowRef = useRef();

    useEffect(() => {
        treeRef.current.addEventListener('scroll', ev => {
            console.log(ev);
            if(treeRef.current.scrollTop === 0) {
                topArrowRef.current.classList.remove('show');
                bottomArrowRef.current.classList.add('show');
            }
            else if(treeRef.current.scrollTop === 764) {
                topArrowRef.current.classList.add('show');
                bottomArrowRef.current.classList.remove('show');
            }
            else {
                topArrowRef.current.classList.add('show');
                bottomArrowRef.current.classList.add('show');
            }
        })
    }, [])

    const devices = [
        {
            name: 'core',
            light: 'light-core.png',
            dark: 'dark-core.png'
        },
        {
            name: 'core2',
            light: 'light-tough.png',
            dark: 'dark-tough.png'
        },
        {
            name: 'stickc',
            light: 'light-stickc.png',
            dark: 'dark-stickc.png'
        },
        {
            name: 'atom',
            light: 'light-atom.png',
            dark: 'dark-atom.png'
        },
        {
            name: 'stickv',
            light: 'light-stickv.png',
            dark: 'dark-stickv.png'
        },
        {
            name: 'timercam',
            light: 'light-timercam.png',
            dark: 'dark-timercam.png'
        },
        {
            name: 'coreink',
            light: 'light-coreink.png',
            dark: 'dark-coreink.png'
        },
        {
            name: 'paper',
            light: 'light-paper.png',
            dark: 'dark-paper.png'
        },
        {
            name: 'stamp',
            light: 'light-stamp.png',
            dark: 'dark-stamp.png'
        }
    ];

    const list = devices.map(device => {
        return (
            <li
                key={device.name}
                className={cs('item-device', selected === device.name ? 'selected' : '')}
                onClick={() => setSelected(device.name)}
            >
                <figure>
                    <img className="avatar" src={'./assets/images/' + device.light} alt={device.name} draggable="false" />
                    <figcaption>{ alias(device.name).toUpperCase() }</figcaption>
                    {/* <span className="own-count">3</span> */}
                </figure>
            </li>
        )
    });
    
    return (
        <div className="list-device-wrapper">
            <div className="top-arrow" ref={topArrowRef}>
                <img src={'./assets/images/top-arrow.png'} alt="top" />
            </div>
            <ul className="list-device" ref={treeRef}>
                { list }
            </ul>
            <div className="bottom-arrow show" ref={bottomArrowRef}>
                <img src={'./assets/images/bottom-arrow.png'} alt="bottom" />
            </div>
        </div>
        
    )
}

export default DeviceList;