export const BurnActionTypes = {
    SET_COM: 'SET_COM',
    SET_BAUDRATE: 'SET_BAUDRATE'
}

export function setCOM(payload) {
    return {
        type: BurnActionTypes.SET_COM,
        ...payload
    }
}

export function setBaudrate(payload) {
    return {
        type: BurnActionTypes.SET_BAUDRATE,
        ...payload
    }
}
