import React, { useState, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import cs from 'classnames';
import { connect } from 'react-redux';
import { CommandTypes, sendCommandWithCustom, sendCommandOnce } from '../../ipc/events';
import { openModal, setAnimation } from '../../actions/modal.action';
import { startStdout, appendStdout } from '../../actions/stdout.action';
import SerialLog from '../SerialLog';
import ConfigForm from '../ConfigForm';
import Loading from '../Loading';
import WifiInput from '../WifiInput';
import { AnimationState, DownloadStatus } from '../../constants';
import DataDemoTextBox from '../DataDemoTextBox/DataDemoTextBox';
import TimerCamCfg from '../TimerCamCfg/TimerCamCfg';
import ImageWallPaperCfg from '../ImageWallPaperCfg/ImageWallPaperCfg';
import PaperWeatherStationCfg from '../PaperWeatherStationCfg/PaperWeatherStationCfg';
import { QRCode } from '../QRCode';
import SmbTimerCamCfg from '../SmbTimerCamCfg';
import AwsOssTimerCamCfg from '../AwsOssTimerCamCfg';

import './FirmwareItem.scss';

const electron = window.require('electron');
const { ipcRenderer, shell } = electron;

let tmpProps = null;

const handleGotoApplyApi = url => {
    shell.openExternal(url);
}

const beforeBurn = props => {
    if(props.firmware.name.indexOf('UIFlow') > -1 || props.firmware.name === 'EchoSTT') {
        let container = document.createElement('div');
        ReactDOM.render(<WifiInput />, container);
        window.swal({
            className: 'wifi-modal',
            icon: 'info',
            content: container,
            closeOnClickOutside: false,
            closeOnEsc: false,
            title: 'Wifi Setting',
            buttons: {
                confirm: {
                    text: 'Cancel',
                    value: false
                },
                cancel: {
                    text: 'Start',
                    value: true,
                    visible: true
                }
            }
        })
        .then(val => {
            if(val) {
                burnHandler(props);
            }
        });
    }
    else if (props.firmware.name === 'TimerCam') {
        let tiemrcamCfgWrapper = document.createElement('div');
        ReactDOM.render(<TimerCamCfg gotoRefer={handleGotoApplyApi} firmware={props.firmware.name} />, tiemrcamCfgWrapper);
        window.swal({
            className: 'timercfg-modal',
            content: tiemrcamCfgWrapper,
            closeOnClickOutside: false,
            closeOnEsc: false,
            title: 'TimerCam Configuration',
            buttons: {
                confirm: {
                    text: 'Cancel',
                    value: 'cancel'
                },
                cancel: {
                    text: 'Burn',
                    value: 'burn',
                    visible: true
                }
            }
        })
        .then(val => {
            if(val === 'burn') {
                let ssid = document.getElementById('timercam__config__ssid').value;
                let password = document.getElementById('timercam__config__password').value;
                let wakeTime = document.getElementById('timercam__config__waketime').value;
                let imageSize = document.getElementById('timercam__config__imagesize').value;
                let payload = { ssid, password, wakeTime, imageSize };
                burnHandler({...props, other: {...payload}});
            }
        });
    }
    else if(props.firmware.name === 'TimerCamAliyun') {
        let tiemrcamCfgWrapper = document.createElement('div');
        ReactDOM.render(<TimerCamCfg gotoRefer={handleGotoApplyApi} firmware={props.firmware.name} />, tiemrcamCfgWrapper);
        window.swal({
            className: 'timercfg-modal',
            content: tiemrcamCfgWrapper,
            closeOnClickOutside: false,
            closeOnEsc: false,
            title: 'TimerCam Configuration',
            buttons: {
                confirm: {
                    text: 'Cancel',
                    value: 'cancel'
                },
                cancel: {
                    text: 'Burn',
                    value: 'burn',
                    visible: true
                }
            }
        })
        .then(val => {
            if(val === 'burn') {
                let ssid = document.getElementById('timercam__config__ssid').value;
                let password = document.getElementById('timercam__config__password').value;
                let wakeTime = document.getElementById('timercam__config__waketime').value;
                let imageSize = document.getElementById('timercam__config__imagesize').value;
                let accessKey = document.getElementById('timercam__config__access_key').value;
                let accessKeySecret = document.getElementById('timercam__config__access_key_secret').value;
                let serverAddr = document.getElementById('timercam__config__server_address').value;
                let bucket = document.getElementById('timercam__config__bucket').value;
                let payload = { ssid, password, wakeTime, imageSize, accessKey, accessKeySecret, serverAddr, bucket };
                burnHandler({...props, other: {...payload}});
            }
        });
    }
    else if(props.firmware.name === 'TimerFolderPusher') {
        let tiemrcamCfgWrapper = document.createElement('div');
        ReactDOM.render(<SmbTimerCamCfg gotoRefer={handleGotoApplyApi} firmware={props.firmware.name} />, tiemrcamCfgWrapper);
        window.swal({
            className: 'timercfg-modal',
            content: tiemrcamCfgWrapper,
            closeOnClickOutside: false,
            closeOnEsc: false,
            title: 'Configuration',
            buttons: {
                confirm: {
                    text: 'Cancel',
                    value: 'cancel'
                },
                cancel: {
                    text: 'Burn',
                    value: 'burn',
                    visible: true
                }
            }
        })
        .then(val => {
            if(val === 'burn') {
                let ssid = document.getElementById('smbtimercam__config__ssid').value;
                let password = document.getElementById('smbtimercam__config__password').value;
                let host = document.getElementById('smbtimercam__config__host').value;
                let user = document.getElementById('smbtimercam__config__user').value;
                let smbpassword = document.getElementById('smbtimercam__config__smbpassword').value;
                let path = document.getElementById('smbtimercam__config__path').value;
                let prefix = document.getElementById('smbtimercam__config__prefix').value;
                let imageSize = document.getElementById('smbtimercam__config__imagesize').value;
                let interval = document.getElementById('smbtimercam__config__interval').value;
                let payload = { ssid, password, host, user, smbpassword, path, prefix, imageSize, interval };
                burnHandler({...props, other: {...payload}});
            }
        });
    }
    else if(props.firmware.name === 'TimerS3FolderPusher') {
        let tiemrcamCfgWrapper = document.createElement('div');
        ReactDOM.render(<AwsOssTimerCamCfg />, tiemrcamCfgWrapper);
        window.swal({
            className: 'timercfg-modal',
            content: tiemrcamCfgWrapper,
            closeOnClickOutside: false,
            closeOnEsc: false,
            title: 'Configuration',
            buttons: {
                confirm: {
                    text: 'Cancel',
                    value: 'cancel'
                },
                cancel: {
                    text: 'Burn',
                    value: 'burn',
                    visible: true
                }
            }
        })
        .then(val => {
            if(val === 'burn') {
                let ssid = document.getElementById('awsosstimercam__config__ssid').value;
                let password = document.getElementById('awsosstimercam__config__password').value;
                let url = document.getElementById('awsosstimercam__config__url').value.replace('https://', '').replace('http://', '');
                let host = url.substring(0, url.indexOf('/'));
                let path = url.substring(url.indexOf('/'));
                let accessKey = document.getElementById('awsosstimercam__config__access_key').value;
                let accessKeySecret = document.getElementById('awsosstimercam__config__access_key_secret').value;
                let prefix = document.getElementById('awsosstimercam__config__prefix').value;
                let imageSize = document.getElementById('awsosstimercam__config__imagesize').value;
                let interval = document.getElementById('awsosstimercam__config__interval').value;
                let payload = { ssid, password, accessKey, accessKeySecret, host, path, prefix, imageSize, interval };
                burnHandler({...props, other: {...payload}});
            }
        });
    }
    else if(props.type === 'paper' && props.firmware.name === 'OnlineImage') {
        let imagewpCfgWrapper = document.createElement('div');
        ReactDOM.render(<ImageWallPaperCfg />, imagewpCfgWrapper);
        window.swal({
            className: 'timercfg-modal',
            content: imagewpCfgWrapper,
            closeOnClickOutside: false,
            closeOnEsc: false,
            title: 'OnlineImage Configuration',
            buttons: {
                confirm: {
                    text: 'Cancel',
                    value: 'cancel'
                },
                cancel: {
                    text: 'Burn',
                    value: 'burn',
                    visible: true
                }
            }
        })
        .then(val => {
            if(val === 'burn') {
                let ssid = document.getElementById('imagewp__config__ssid').value;
                let password = document.getElementById('imagewp__config__password').value;
                let wakeTime = document.getElementById('imagewp__config__waketime').value;
                let url = document.getElementById('imagewp__config__url').value;
                if(url.trim() === '') {
                    window.swal({
                        text: 'Image url is required.',
                        icon: 'warning'
                    });
                    return;
                }
                let payload = { ssid, password, wakeTime, url };
                burnHandler({...props, other: {...payload}});
            }
        });
    }
    else if(props.type === 'paper' && props.firmware.name === 'WeatherStation') {
        let paperwsCfgWrapper = document.createElement('div');
        ReactDOM.render(<PaperWeatherStationCfg gotoApplyApi={handleGotoApplyApi} />, paperwsCfgWrapper);
        window.swal({
            className: 'timercfg-modal',
            content: paperwsCfgWrapper,
            closeOnClickOutside: false,
            closeOnEsc: false,
            title: 'Weather Station Configuration',
            buttons: {
                confirm: {
                    text: 'Cancel',
                    value: 'cancel'
                },
                cancel: {
                    text: 'Burn',
                    value: 'burn',
                    visible: true
                }
            }
        })
        .then(val => {
            if(val === 'burn') {
                let ssid = document.getElementById('paperws__config__ssid').value;
                let password = document.getElementById('paperws__config__password').value;
                let latitude = document.getElementById('paperws__config__latitude').value;
                let longitude = document.getElementById('paperws__config__longitude').value;
                let language = document.getElementById('paperws__config__language').value;
                let apikey = document.getElementById('paperws__config__apikey').value;
                if(typeof parseFloat(latitude) !== 'number') {
                    window.swal({
                        text: 'Invalid latitude.',
                        icon: 'warning'
                    });
                    return;
                }
                if(typeof parseFloat(longitude) !== 'number') {
                    window.swal({
                        text: 'Invalid longitude.',
                        icon: 'warning'
                    });
                    return;
                }
                if(apikey === '' || apikey === undefined) {
                    window.swal({
                        text: 'Invalid Apikey.',
                        icon: 'warning'
                    });
                    return;
                }
                if(password === '') {
                    password = 'null';
                }
                let payload = { ssid, password, latitude, longitude, apikey, language };
                burnHandler({...props, other: {...payload}});
            }
        });
    }
    else {
        burnHandler(props);
    }
}

const burnHandler = (props) => {
    let closeModalHandler = props.closeModal;
    let payload = {
        com: props.com,
        name: props.firmware.name,
        version: props.version,
        type: props.type,
        other: props.other || null
    }

    let ssid = localStorage.getItem('ssid');
    let password = localStorage.getItem('password');
    if (ssid != undefined && password != undefined && ssid != '' && password != '') {
        payload.ssid = ssid;
        payload.password = password;
    }

    const footerButtons = (function () {
        return (
            <>
                <div className="footer-btn">
                    <button onClick={() => {
                        ipcRenderer.send(CommandTypes.killTask, null);
                        let sub = `${CommandTypes.burn}Callback-${payload.com}`;
                        if (ipcRenderer.eventNames().includes(sub)) {
                            ipcRenderer.off(sub, ipcRenderer.listeners(sub)[0]);
                        }
                        closeModalHandler();
                    }}>Close</button>
                </div>
            </>
        )
    })();

    props.openModal(<SerialLog footer={footerButtons} com={payload.com} />);

    props.startStdoutMessage(`=================\nStart Burn\n=================\n`);
    sendCommandWithCustom(payload.com, CommandTypes.burn, payload, data => {
        props.appendStdoutMessage(data);
    });
    ipcRenderer.once('notify-bind', (ev, data) => {
        if(data === false) {
            // 
            window.swal({
                className: 'binding-tip-modal',
                title: '',
                text: 'It is detected that this device is still unbound, whether to bind to the account now?',
                icon: 'warning',
                buttons: {
                    confirm: {
                        text: 'Yes',
                        value: true
                    },
                    cancel: {
                        text: 'No',
                        value: false,
                        visible: true
                    }
                }
            })
            .then(ret => {
                if(ret) {
                    window.swal({
                        title: '',
                        text: 'Adding device, wait a minute...',
                        icon: 'warning',
                        closeOnClickOutside: false,
                        closeOnEsc: false,
                        buttons: false
                    })
            
                    sendCommandOnce(CommandTypes.bindDevice, {
                        com: props.com,
                        firmware: props.firmware.name,
                        firmwareVersion: props.version,
                        type: props.type
                    }, ret => {
                        if(ret === -1) {
                            // 
                            this.props.setLoginState(false);
                            window.swal({
                                title: '',
                                text: 'The login status has expired, please log in again',
                                icon: 'warning'
                            })
                            return;
                        }
                        else if(ret === false) {
                            window.swal({
                                title: '',
                                text: 'Invalid device',
                                icon: 'error'
                            })
                        }
                        else if(ret === true) {
                            window.swal({
                                title: '',
                                text: 'Device has been added in your account.',
                                icon: 'success'
                            })
                        }
                        else if(ret === -1000) {
                            window.swal({
                                title: '',
                                text: 'Network occured errors',
                                icon: 'error'
                            })
                        }
                        else if(ret === 400) {
                            window.swal({
                                className: 'binding-tip-modal',
                                title: '',
                                text: 'This device has been bound, try again after unbinding.',
                                icon: 'error'
                            })
                        }
                        else {
                            window.swal({
                                title: '',
                                text: 'Network occured errors',
                                icon: 'error'
                            })
                        }
                    });
                }
            })
        }
    });
}

const getTokenHandler = (props) => {
    tmpProps = props;

    let payload = {
        com: props.com
    }

    window.swal({
        title: 'Tips',
        text: 'Getting token ...',
        icon: 'warning',
        buttons: false
    });

    let modalOpts = {};
    sendCommandWithCustom(payload.com, CommandTypes.getSttToken, payload, data => {
        if (data.state === 1) {
            if(tmpProps.device === 'timercam') {
                let wrapper = document.createElement('div');
                ReactDOM.render(<QRCode token={data.message} />, wrapper);
                modalOpts = {
                    content: wrapper,
                    buttons: {
                        confirm: {
                            text: 'Copy Token',
                            value: true,
                            closeModal: false,
                            className: '__token_copy'
                        },
                        view: {
                            text: 'View in browser',
                            value: 'view',
                            closeModal: false,
                            visible: true
                        },
                        cancel: {
                            text: 'Ok',
                            value: false,
                            visible: true
                        }
                    }
                }
            } else {
                modalOpts = {
                    title: 'Success',
                    text: `Token: ${data.message}`,
                    icon: 'success',
                    buttons: {
                        confirm: {
                            text: 'Copy',
                            value: true,
                            closeModal: false,
                            className: '__token_copy'
                        },
                        cancel: {
                            text: 'Ok',
                            value: false,
                            visible: true
                        }
                    }
                }
            }

            window.swal(modalOpts)
            .then(value => {
                if(value === 'view') {
                    shell.openExternal(`http://api.m5stack.com:5003/timer-cam/image?tok=${data.message}`);
                    window.swal.stopLoading();
                    return;
                }
                if(value) {
                    new window['ClipboardJS']('.__token_copy', {
                        text: function() {
                            return data.message;
                        }
                    });
                    window.swal.stopLoading();
                    let copyBtn = document.getElementsByClassName('__token_copy')[0];
                    copyBtn.innerText = 'Copied';
                }
            });
        } else {
            window.swal({
                title: 'Error',
                text: `Get token failed. ${data.message}.`,
                icon: 'error',
                buttons: {
                    confirm: {
                        text: 'Cancel'
                    }
                }
            })
        }
    });
}

const downloadButton = (props) => {
    let { selectedVersion } = props;
    if (!selectedVersion.downloadStatus) {
        return (
            <>
                <i className="iconfont icon-clouddownload"></i>
                <span>Download</span>
            </>
        );
    }
    switch (selectedVersion.downloadStatus) {
        case DownloadStatus.DOWNLOADING:
            let styles = {
                width: (115 * selectedVersion.progress) + 'px'
            }
            return (
                <>
                    <div className="progress" style={styles}></div>
                    <span>Downloading</span>
                </>
            );
        case DownloadStatus.ERROR:
            return (
                <>
                    <i className="iconfont icon-clouddownload"></i>
                    <span>Retry</span>
                </>
            );
        default:
            return (
                <>
                    <i className="iconfont icon-clouddownload"></i>
                    <span>Download</span>
                </>
            );
    }
}

const configurateHandle = (props) => {
    let closeModalHandler = props.closeModal;
    let payload = {
        com: props.com,
        name: props.firmware.name,
        version: props.version,
        type: props.type
    }

    props.openModal(<Loading />);

    sendCommandWithCustom(props.com, CommandTypes.getUIFlowSetting, payload, data => {
        props.closeModal();

        // check enable read data in this com
        if (data === 'Error') {
            window.swal({
                title: 'Tips',
                text: 'Get UIFlow settings failed, check your COM and retry.',
                icon: 'error',
                buttons: {
                    confirm: {
                        text: 'Cancel'
                    }
                }
            })
            return;
        }

        let json = {};
        // check data validate
        try {
            json = JSON.parse(data);
        } catch {
            window.swal({
                title: 'Tips',
                text: 'Invalid data received, check your device.',
                icon: 'error',
                buttons: {
                    confirm: {
                        text: 'Cancel'
                    }
                }
            })
            return;
        }

        // Successfully. Must to open after loading modal.
        setTimeout(() => {
            props.openModal(<ConfigForm
                deviceType={props.type}
                data={json}
                onClose={() => {
                    let sub = `${CommandTypes.getUIFlowSetting}Callback-${payload.com}`;
                    if (ipcRenderer.eventNames().includes(sub)) {
                        ipcRenderer.off(sub, ipcRenderer.listeners(sub)[0]);
                    }
                    closeModalHandler();
                }}
                onSave={option => {
                    beforeSave(props, payload, option)
                }}
            />);
        }, 300);
    });
}

function beforeSave(props, payload, option) {
    if(option.APN === undefined) {
        option.APN = '';
    }
    if(option.APN === '') {
        window.swal({
            title: 'Tips',
            text: 'APN is required, if you continue, "CMNET" will be used by default.',
            icon: 'warning',
            buttons: {
                confirm: {
                    text: 'Continue',
                    value: 1
                },
                cancel: {
                    text: 'Back',
                    value: 0,
                    visible: true
                }
            }
        })
        .then(val => {
            if(val === 1) {
                option.APN = 'CMNET';
                saveSettingHandle(props, payload, option);
            }
        })
    } else {
        saveSettingHandle(props, payload, option);
    }
}

function saveSettingHandle(props, payload, option) {
    props.closeModal();
    window.swal({
        title: 'Tips',
        text: 'Writing data, do not close.',
        icon: 'warning',
        buttons: false
    });
    sendCommandWithCustom(props.com, CommandTypes.setUIFlowSetting, { ...payload, option: { ...option } }, data => {
        let sub = `${CommandTypes.setUIFlowSetting}Callback-${payload.com}`;
        if (ipcRenderer.eventNames().includes(sub)) {
            ipcRenderer.off(sub, ipcRenderer.listeners(sub)[0]);
        }
        if (data === 'Ok') {
            window.swal({
                title: 'Success',
                text: 'Update successfully.',
                icon: 'success'
            });
        } else {
            window.swal({
                title: 'Failed',
                text: 'Update falied.',
                icon: 'error'
            });
        }
    });
}

function FirmwareItem(props) {
    let { firmware, downloadHanlder, removeHandler, device } = props;
    firmware.versions.sort((a, b) => Date.parse(new Date(b.publishAt)) - Date.parse(new Date(a.publishAt)));
    let [selectedVersion, setSelectedVersion] = useState({});
    let selectRef = useRef(null);

    useEffect(() => {
        let index = 0;
        if (selectRef != null) {
            index = firmware['versions'].findIndex(v => v.version === selectRef.current.value)
        }
        setSelectedVersion(firmware['versions'][index]);
    }, [firmware, selectedVersion]);

    const versionSelector = firmware.versions
        .map(v => {
            return (<option key={v.version}>{v.version}</option>)
        });

    const selectVersion = ev => {
        setSelectedVersion(props.firmware.versions.find(v => v.version === ev.target.value));
    }

    const btnGetToken = () => {
        if (firmware.name.toUpperCase() === 'ECHOSTT' || firmware.name.toUpperCase() === 'TIMERCAM') {
            return (
                <button
                    className="btn remove"
                    onClick={() => {
                        getTokenHandler(props);
                    }}
                >
                    <div className="mask"></div>
                    <span>Get Token</span>
                </button>
            )
        } else if (firmware.name.indexOf('UIFlow') > -1 ) {
            return (
                <button
                    className="btn remove"
                    onClick={() => {
                        configurateHandle({
                            ...props,
                            version: selectedVersion.version,
                            type: device
                        });
                    }}
                >
                    <div className="mask"></div>
                    <i className="iconfont icon-setting"></i>
                    <span>Configuration</span>
                </button>
            )
        }
        return null;
    }

    const btnShowDataDemo = () => {
        return (
            <button
                className={cs('btn', 'info')}
                onClick={() => {
                    let container = document.createElement('div');
                    ReactDOM.render(<DataDemoTextBox value={firmware.dataDemo} />, container);

                    window.swal({
                        content: container,
                        className: 'data-demo-modal'
                    })
                    .then(val => {

                    });
                }}
            >
                <div className="mask"></div>
                <span>Data Demo</span>
            </button>
        )
    }

    let downloadCounter = parseInt(firmware.downloaded);
    let downloadText = '';
    if(!isNaN(downloadCounter)) {
        if(downloadCounter >= 10000) {
            downloadText = (downloadCounter / 1000).toFixed(1) + 'k';
        } else {
            downloadText = downloadCounter.toString();
        }
    }

    let imgErrorHandler = (ev, url) => {
        ev.persist();
        ev.target.src = './assets/cover/default.png';
        let timer = setTimeout(() => {
            ev.target.src = url;
            clearTimeout(timer);
        }, 5000);
    }

    let openLinkHandler = (ev, url) => {
        ev.preventDefault();
        shell.openExternal(url);
    }

    return (
        <div className={cs('wrapper-item-firmware', 'fake-out')}>
            <article className="item">
                <img className="cover"
                    src={firmware.cover ? `./assets/cover/${firmware.cover}` : './assets/cover/default.png'}
                    onError={ev => imgErrorHandler(ev, `./assets/cover/${firmware.cover}`)}
                    draggable="false"
                    onMouseOver={ev => {
                        if(ev.target.__firstInited && !ev.target.__played) {
                            ev.target.__played = true;
                            ev.target.play();
                        }
                    }}
                    onMouseLeave={ev => {
                        if(ev.target.__firstInited && ev.target.__played) {
                            ev.target.__played = false;
                            ev.target.stop();
                        }
                    }}
                    onLoad={ev => {
                        if(ev.target.__firstInited) return;
                        if(ev.target.src.endsWith('.gif')) {
                            ev.target.__firstInited = true;
                            ev.target.__played = false;
                            ev.target.stop();
                        }
                    }}
                />
                <h2>
                    <span className="name">{firmware.name}</span>
                    {
                        firmware.publisher === 'M5Stack' ?
                            <span className="tag">official</span> : null
                    }
                    <select className="selector-version" onChange={selectVersion} ref={selectRef}>
                        {versionSelector}
                    </select>
                    {
                        firmware.repoLink ?
                        (
                            <a className="link" href={firmware.repoLink} onClick={ev => openLinkHandler(ev, firmware.repoLink)}>
                                <i className="iconfont icon-github github"></i>
                            </a>
                        )
                        : null
                    }
                </h2>
                <p>{firmware.description}</p>
                <div className="footer">
                    {/* <span className="more">More</span> */}
                    {
                        selectedVersion['exist'] ?
                            <>
                                <button
                                    className="btn remove"
                                    onClick={() => removeHandler({
                                        name: firmware.name,
                                        version: selectedVersion.version
                                    })}
                                >
                                    <div className="mask"></div>
                                    <i className="iconfont icon-delete"></i>
                                    <span>Remove</span>
                                </button>
                                {btnGetToken()}
                                {device === 'stickv' ? btnShowDataDemo() : null}
                                <button
                                    className="btn burn"
                                    onClick={() => beforeBurn({
                                        ...props,
                                        version: selectedVersion.version,
                                        type: device
                                    })}
                                >
                                    <div className="mask"></div>
                                    <i className="iconfont icon-hot-or-burn-interface-symbol"></i>
                                    <span>Burn</span>
                                </button>
                            </>
                            :
                            <>
                                {device === 'stickv' ? btnShowDataDemo() : null}
                                <button
                                    className={cs('btn', 'download', selectedVersion.downloadStatus === DownloadStatus.DOWNLOADING ? 'downloading' : '')}
                                    onClick={() => downloadHanlder({
                                        name: firmware.name,
                                        version: selectedVersion.version,
                                        type: device
                                    })}
                                >
                                    <div className="mask"></div>
                                    {downloadButton({ selectedVersion })}
                                </button>
                            </>
                    }
                </div>
                <div className="remark">
                    <div>
                        <div className="left">
                            <span>Author: {firmware.publisher}</span>
                        </div>
                        <div className="right">
                            <i className="iconfont icon-clouddownload"></i>
                            <span>{downloadText}</span>
                        </div>
                        <div className="clear-fix"></div>
                    </div>
                </div>
            </article>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        com: state.burn.com,
        selectedType: state.firmware.selected
    }
}

const mapDispatchToProps = dispatch => {
    return {
        openModal: component => dispatch(openModal({ component })),
        closeModal: _ => dispatch(setAnimation({ animation: AnimationState.FADE_OUT })),
        startStdoutMessage: message => dispatch(startStdout({ message })),
        appendStdoutMessage: message => dispatch(appendStdout({ message }))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(FirmwareItem);