import { BurnActionTypes } from '../actions/burn.action';

const initialState = {
    com: '',
    baudrate: '1500000'
}

export default function reducer(state = initialState, action) {
    switch(action.type) {
        case BurnActionTypes.SET_COM:
            return {
                ...state,
                com: action.com
            }

        case BurnActionTypes.SET_BAUDRATE:
            return {
                ...state,
                baudrate: action.baudrate
            }
        
        default:
            return state;
    }
}