import React, { useState } from 'react';

import './PaperWeatherStationCfg.scss';

export default props => {
    const [ssid, setSSID] = useState(localStorage.getItem('ssid') || '');
    const [pwd, setPwd] = useState(localStorage.getItem('password') || '');
    const [latitude, setLatitude] = useState(localStorage.getItem('paperws_latitude') || '');
    const [longitude, setLongitude] = useState(localStorage.getItem('paperws_longitude') || '');
    const [language, setLanguage] = useState('en_US');
    const [apikey, setApikey] = useState('');
    const [pwdVisible, setPwdVisible] = useState(false);
    const togglePwdVisible = () => setPwdVisible(!pwdVisible);
    const saveSSID = ev => {
        setSSID(ev.target.value);
        localStorage.setItem('ssid', ev.target.value);
    }
    const savePwd = ev => {
        setPwd(ev.target.value);
        localStorage.setItem('password', ev.target.value);
    }
    const saveLatitude = ev => {
        setLatitude(ev.target.value);
        localStorage.setItem('paperws_latitude', ev.target.value);
    }
    const saveLongitude = ev => {
        setLongitude(ev.target.value);
        localStorage.setItem('paperws_longitude', ev.target.value);
    }

    return (
        <>
            <table className="wrapper-timercambox">
                <tbody>
                    <tr className="wifibox-item">
                        <td className="field-name">SSID:</td>
                        <td>
                            <input id="paperws__config__ssid" type="text" value={ssid} onChange={saveSSID} />
                        </td>
                    </tr>
                    <tr className="wifibox-item password">
                        <td className="field-name">Password:</td>
                        <td>
                            <input id="paperws__config__password" type={!pwdVisible?"password":"text"} value={pwd} onChange={savePwd} />
                            <img
                                className="btn-eye"
                                src={!pwdVisible ? "./assets/images/show.png" : "./assets/images/hide.png"}
                                onClick={togglePwdVisible}
                                alt=""
                            />
                        </td>
                    </tr>
                    <tr className="wifibox-item">
                        <td className="field-name">Language:</td>
                        <td>
                            <select id="paperws__config__language" value={language} onChange={ev => setLanguage(ev.target.value)}>
                                <option value="en_US">English</option>
                                <option value="zh_CN"></option>
                                <option value="ja"></option>
                            </select>
                        </td>
                    </tr>
                    <tr className="wifibox-item">
                        <td className="field-name">Longitude:</td>
                        <td>
                            <input
                                id="paperws__config__longitude"
                                type="text"
                                value={longitude}
                                placeholder="e.g. 98.765432"
                                onChange={saveLongitude}
                            />
                        </td>
                    </tr>
                    <tr className="wifibox-item">
                        <td className="field-name">Latitude:</td>
                        <td>
                            <input
                                id="paperws__config__latitude"
                                type="text" value={latitude}
                                placeholder="e.g. 123.456789"
                                onChange={saveLatitude}
                            />
                        </td>
                    </tr>
                    <tr className="wifibox-item">
                        <td className="field-name">Apikey:</td>
                        <td>
                            <input  
                                id="paperws__config__apikey"
                                type="text"
                                value={apikey}
                                onChange={ev => setApikey(ev.target.value)}
                            />
                        </td>
                    </tr>
                    <tr className="wifibox-item">
                        <td></td>
                        <td style={{padding:0}}>
                            <span style={{'fontSize':12,color:'#989898'}}>
                                Apply to use the API here
                                <a style={{marginLeft:4,cursor:'pointer',textDecoration:'underline'}}
                                    onClick={() => props.gotoApplyApi('http://www.caiyun.org/')}>http://www.caiyun.org/</a>
                            </span>
                        </td>
                    </tr>
                </tbody>
            </table>
        </>
    )
}
