import { StdoutActionTypes } from "../actions/stdout.action";

const initialState = {
    message: ''
}

export default function reducer(state = initialState, action) {
    switch(action.type) {
        case StdoutActionTypes.START_STDOUT:
            return {
                message: action.payload.message
            }
        case StdoutActionTypes.APPEND_STDOUT:
            return {
                message: state.message + action.payload.message
            }
        default:
            return state;
    }
}