import React, { useEffect, useRef } from 'react';
import FirmwareItem from '../FirmwareItem';

import './FirmwareList.scss';

function FirmwareList(props) {
    const { firmwareList,  downloadHanlder, removeHandler, device } = props;
    const listRef = useRef();
    useEffect(() => {
        function scrollHandler(ev) {
            ev.preventDefault();
            listRef.current.style.transform = `translateY(-${ev.srcElement.scrollTop}px)`;
        }
        listRef.current.parentElement.addEventListener('scroll', scrollHandler);
        return () => {
            listRef.current.parentElement.removeEventListener('scroll', scrollHandler);
        }
    }, []);
    if(!firmwareList) {
        return (
            <div className="list-firmware"></div>
        )
    }
    const list = firmwareList.map((firmware, index) => {
        return (
            <FirmwareItem
                key={device + '-' +firmware.name}
                device={device}
                firmware={firmware}
                downloadHanlder={downloadHanlder}
                removeHandler={removeHandler}
            />
        )
    });
    return (
        <div className="list-firmware" ref={listRef}>
            { list }
        </div>
    )
}

export default FirmwareList;