export const GlobalActionTypes = {
    SET_THEME: 'SET_THEME',
    SET_LANGUAGE: 'SET_LANGUAGE',
    SET_BURN_PARAMS: 'SET_BURN_PARAMS',
    SET_VERSION: 'SET_VERSION',
    SET_LASTEST_VERSION: 'SET_LASTEST_VERSION',
    SET_LOGIN_STATE: 'SET_LOGIN_STATE',
    SET_OWNS: 'SET_OWNS'
}

export function setTheme(payload) {
    return {
        type: GlobalActionTypes.SET_THEME,
        payload
    }
}

export function setLanguage(payload) {
    return {
        type: GlobalActionTypes.SET_LANGUAGE,
        payload
    }
}

export function setBurnParams(payload) {
    return {
        type: GlobalActionTypes.SET_BURN_PARAMS,
        payload
    }
}

export function setVersion(payload) {
    return {
        type: GlobalActionTypes.SET_VERSION,
        payload
    }
}

export function setLastestVersion(payload) {
    return {
        type: GlobalActionTypes.SET_LASTEST_VERSION,
        payload
    }
}

export function setLoginState(payload) {
    return {
        type: GlobalActionTypes.SET_LOGIN_STATE,
        payload
    }
}

export function setOwns(payload) {
    return {
        type: GlobalActionTypes.SET_OWNS,
        payload
    }
}
