import React, { useState } from 'react';

import './AwsOssTimerCamCfg.scss';

const IMAGE_SIZE_ENUM = [
    {text: '1920x1080', value: 14},
    {text: '1280x720', value: 11},
    {text: '640x480', value: 8}
]

const AwsOssTimerCamCfg = ({}) => {
    const [ssid, setSSID] = useState(localStorage.getItem('ssid') || '');
    const [pwd, setPwd] = useState(localStorage.getItem('password') || '');
    const [accessKey, setAccessKey] = useState('');
    const [accessKeySecret, setAccessKeySecret] = useState('');
    const [url, setUrl] = useState('');
    const [prefix, setPrefix] = useState('pic');
    const [imageSize, setImageSize] = useState(14);
    const [interval, setInterval] = useState(20);
    const [pwdVisible, setPwdVisible] = useState(false);

    const togglePwdVisible = () => setPwdVisible(!pwdVisible);
    const saveSSID = ev => {
        setSSID(ev.target.value);
        localStorage.setItem('ssid', ev.target.value);
    }
    const savePwd = ev => {
        setPwd(ev.target.value);
        localStorage.setItem('password', ev.target.value);
    }
    const ImageSizeList = (() => {
        return IMAGE_SIZE_ENUM.map((item, index) => <option key={index} value={item.value}>{item.text}</option>);
    })();

    return (<>
        <table className="wrapper-timercambox">
            <tbody>
                <tr className="wifibox-item">
                    <td className="field-name">WIFI SSID:</td>
                    <td>
                        <input id="awsosstimercam__config__ssid" type="text" value={ssid} onChange={saveSSID} />
                    </td>
                </tr>
                <tr className="wifibox-item password">
                    <td className="field-name">WIFI Password:</td>
                    <td>
                        <input id="awsosstimercam__config__password" type={!pwdVisible?"password":"text"} value={pwd} onChange={savePwd} />
                        <img
                            className="btn-eye"
                            src={!pwdVisible ? "./assets/images/show.png" : "./assets/images/hide.png"}
                            onClick={togglePwdVisible}
                            alt=""
                        />
                    </td>
                </tr>
                <tr className="wifibox-item">
                    <td className="field-name">Access Key:</td>
                    <td>
                        <input
                            id="awsosstimercam__config__access_key"
                            type="text"
                            value={accessKey}
                            onChange={ev => setAccessKey(ev.target.value)}
                        />
                    </td>
                </tr>
                
                <tr className="wifibox-item">
                    <td className="field-name">Secret Key:</td>
                    <td>
                        <input
                            id="awsosstimercam__config__access_key_secret"
                            type="text"
                            value={accessKeySecret}
                            onChange={ev => setAccessKeySecret(ev.target.value)}
                        />
                    </td>
                </tr>

                <tr className="wifibox-item">
                    <td className="field-name">URL:</td>
                    <td>
                        <input
                            id="awsosstimercam__config__url"
                            type="text"
                            value={url}
                            onChange={ev => setUrl(ev.target.value)}
                            placeholder="eg: example.s3.amazonaws.com/example"
                        />
                    </td>
                </tr>

                <tr className="wifibox-item">
                    <td className="field-name">FilePrefix:</td>
                    <td>
                        <input id="awsosstimercam__config__prefix" type="text" value={prefix} onChange={ev => setPrefix(ev.target.value)} />
                    </td>
                </tr>

                <tr className="wifibox-item">
                    <td className="field-name">Image Size:</td>
                    <td>
                        <select id="awsosstimercam__config__imagesize" value={imageSize} onChange={ev => setImageSize(ev.target.value)}>
                            {ImageSizeList}
                        </select>
                    </td>
                </tr>
                <tr className="wifibox-item">
                    <td className="field-name">Interval:</td>
                    <td>
                        <input id="awsosstimercam__config__interval" type="number" value={interval} onChange={ev => setInterval(ev.target.value)} />
                        <span>(senconds)</span>
                    </td>
                </tr>
            </tbody>
        </table>
    </>)
}

export default AwsOssTimerCamCfg;
