import React, { useEffect, useRef } from 'react';
import { connect } from 'react-redux';

import './SerialLog.scss';

function SerialLog(props) {
    const logRef = useRef(null);

    useEffect(() => {
        logRef.current.scrollTo(0, logRef.current.scrollHeight);
    }, [props.stdout.message]);

    return (
        <>
            <div className="wrapper-serial-log" ref={logRef}>
                <pre>{props.stdout.message}</pre>
            </div>
            { props.footer }
        </>
    )
}

const mapStateToProps = state => {
    return {
        stdout: state.stdout
    }
}

const mapDispatchToProps = dispatch => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SerialLog);