import { Theme, Language } from '../constants';
import { GlobalActionTypes } from '../actions/global.action';

const initialState = {
    theme: Theme.Light,
    language: Language.en,
    burnParams: {
        flashMode: 'dio',
        flashFrequency: '80m',
        flashSize: 'detect'
    },
    version: '',
    lastest: '',
    loginState: false,
    ssoToken: '',
    owns: []
}

export default function reducer(state = initialState, action) {
    switch(action.type) {
        case GlobalActionTypes.SET_THEME:
            return {
                ...state,
                ...action.payload
            };

        case GlobalActionTypes.SET_LANGUAGE:
            return {
                ...state,
                ...action.payload
            };

        case GlobalActionTypes.SET_BURN_PARAMS:
            return {
                ...state,
                burnParams: {
                    ...state.burnParams,
                    ...action.payload
                }
            };
        case GlobalActionTypes.SET_VERSION:
            return {
                ...state,
                ...action.payload
            }
        case GlobalActionTypes.SET_LASTEST_VERSION:
            return {
                ...state,
                ...action.payload
            }
        case GlobalActionTypes.SET_LOGIN_STATE:
            return {
                ...state,
                ...action.payload
            }

        case GlobalActionTypes.SET_OWNS:
            return {
                ...state,
                ...action.payload
            }

        default:
            return state;
    }
}