export const FirmwareActionTypes = {
    GET_FIRMWARE: 'GET_FIRMWARE',
    UPDATE_FIRMWARES: 'UPDATE_FIRMWARES',
    SELECT_FIRMWARE_TYPE: 'SELECT_FIRMWARE_TYPE',
    UPDATE_DOWNLOAD_STATUS: 'UPDATE_DOWNLOAD_STATUS',
    SEARCH_FIRMWARE: 'SEARCH_FIRMWARE'
}

export function getFirmware(payload) {
    return {
        type: FirmwareActionTypes.GET_FIRMWARE,
        payload
    }
}

export function updateFirmwares(payload) {
    return {
        type: FirmwareActionTypes.UPDATE_FIRMWARES,
        payload
    }
}

export function selectDeviceType(payload) {
    return {
        type: FirmwareActionTypes.SELECT_FIRMWARE_TYPE,
        payload
    }
}

export function updateDownloadStatus(payload) {
    return {
        type: FirmwareActionTypes.UPDATE_DOWNLOAD_STATUS,
        payload
    }
}

export function searchFirmware(payload) {
    return {
        type: FirmwareActionTypes.SEARCH_FIRMWARE,
        payload
    }
}