import React, { useState } from 'react';

import './ConfigForm.scss';

const ConfigForm = props => {
    let [option, setOption] = useState(props.data);
    let [passwordVisible, setPasswordVisible] = useState(false);
    let [copyState, setCopyState] = useState(false);
    const copyHandler = apikey => {
        new window['ClipboardJS']('#copy_uiflow_apikey', {
            text: function() {
                return apikey;
            }
        });
        setCopyState(true);
    }
    return (
        <div className="config-form">
            <h3>UIFlow Config</h3>
            <table className="data-table">
                <tbody>
                    <tr className="field apikey">
                        <td className="key">ApiKey:</td>
                        <td>
                            <span id="uiflow_apikey">{ option.apikey }</span>
                            <img
                                id="copy_uiflow_apikey"
                                className="copy"
                                src="./assets/images/copy.png"
                                onClick={() => copyHandler(option.apikey)}
                            />
                            {
                                copyState
                                ? <span className="text-copy">copied!</span>
                                : null
                            }
                        </td>
                    </tr>
                    <tr className="field mode">
                        <td className="key">Start Mode:</td>
                        <td>
                            <select value={option.start} onChange={ev => setOption({...option, start: +ev.target.value})}>
                                <option value="0">Internet Mode</option>
                                <option value="1">USB Mode</option>
                                <option value="2">App Mode</option>
                            </select>
                        </td>
                    </tr>
                    <tr className="field hold">
                        <td className="key">Boot Menu:</td>
                        <td>
                            <select
                                value={option['startup-hold']}
                                onChange={ev => setOption({...option, 'startup-hold': ev.target.value === 'false' ? false : true})}
                            >
                                <option value="true">True</option>
                                <option value="false">False</option>
                            </select>
                        </td>
                    </tr>
                    <tr className="field server">
                        <td className="key">Server:</td>
                        <td>
                            <select value={option['server']} onChange={ev => setOption({...option, server: ev.target.value})}>
                                <option value="flow.m5stack.com">flow.m5stack.com</option>
                                <option value="sg.flow.m5stack.com">sg.flow.m5stack.com</option>
                            </select>
                        </td>
                    </tr>
                    <tr className="field wifi">
                        <td className="key">Wifi:</td>
                        <td>
                            <div className="wifi-item">
                                <div className="inputbox">
                                    <span>SSID</span>
                                    <input
                                        defaultValue={option.wifi[0]}
                                        onChange={ev => setOption({...option, wifi: [ev.target.value, option.wifi[1]]})}
                                    />
                                </div>
                                <div className="inputbox password">
                                    <span>Password</span>
                                    <input
                                        type={!passwordVisible ? 'password' : 'text'}
                                        defaultValue={option.wifi[1]}
                                        onChange={ev => setOption({...option, wifi: [option.wifi[0], ev.target.value]})}
                                    />
                                    <img
                                        onClick={() => setPasswordVisible(!passwordVisible)}
                                        className="btn-eye"
                                        src={passwordVisible ? './assets/images/hide.png' : './assets/images/show.png'}
                                    />
                                </div>
                            </div>
                        </td>
                    </tr>
                    {
                        props.deviceType === 'core'
                        ?
                        <>
                            <tr className="field comx">
                                <td className="key">COM.X:</td>
                                <td>
                                    <select value={option['COMX']} onChange={ev => setOption({...option, COMX: ev.target.value})}>
                                        <option value="false">False</option>
                                        <option value="true">True</option>
                                    </select>
                                </td>
                            </tr>
                            <tr className="field apn">
                                <td className="key">APN:</td>
                                <td>
                                    <input
                                        defaultValue={option.APN}
                                        onChange={ev => setOption({...option, APN: ev.target.value})}
                                    />
                                </td>
                            </tr>
                        </>
                        : null
                    }
                </tbody>
            </table>
            <div className="footer">
                <button className="btn-close" onClick={props.onClose}>Cancel</button>
                <button className="btn-save" onClick={() => props.onSave(option)}>Save</button>
            </div>
        </div>
    )
}

export default ConfigForm;