import React, { useEffect, useRef } from 'react';
import { connect } from 'react-redux';
import { Theme } from '../../constants';

import './PreferencePanel.scss';

function PreferencePanel(props) {
    const { onClose, config, onChange, onUpdate } = props;
    const panelRef = useRef();

    useEffect(() => {
        const timer = setTimeout(() => {
            panelRef.current.classList.add('slide-in');
        });
        return () => {
            panelRef.current.classList.remove('slide-in');
            panelRef.current.classList.add('slide-out');
            clearTimeout(timer);
        }
    }, []);

    const hidePanelHandler = () => {
        panelRef.current.classList.remove('slide-in');
        panelRef.current.classList.add('slide-out');
        setTimeout(() => {
            onClose();
        }, 250);
    }

    const changeTheme = ev => {
        onChange('theme', ev.target.value);
    }

    const changeBurnParams = (key, value) => {
        onChange(key, value);
    }

    return (
        <div className="wrapper-panel-preference" onClick={hidePanelHandler}>
            <div className="panel-preference" ref={panelRef} onClick={ev => ev.stopPropagation()}>
                <h2>Preference</h2>
                <form className="form-preference">
                    <h3>General</h3>
                    <div className="form-item">
                        <label>Theme</label>
                        <select value={config.theme} onChange={changeTheme}>
                            <option value={Theme.Light}>Light</option>
                            <option value={Theme.Dark}>Dark</option>
                        </select>
                    </div>
                    <h3>Advanced</h3>
                    <div className="form-item">
                        <label>Flash Frequency</label>
                        <select
                            value={config.burnParams.flashFrequency}
                            onChange={ev => changeBurnParams('flashFrequency', ev.target.value)}
                        >
                            <option value="80m">80m</option>
                            <option value="40m">40m</option>
                            <option value="26m">26m</option>
                            <option value="20m">20m</option>
                            <option value="keep">Keep</option>
                        </select>
                    </div>
                    <div className="form-item">
                        <label>Flash Size</label>
                        <select
                            value={config.burnParams.flashSize}
                            onChange={ev => changeBurnParams('flashSize', ev.target.value)}
                        >
                            <option value="detect">Detect</option>
                            <option value="16MB">16MB</option>
                            <option value="4MB">4MB</option>
                            <option value="2MB">2MB</option>
                            <option value="1MB">1MB</option>
                        </select>
                    </div>
                    <h3>Version</h3>
                    <div className="form-item">
                        <label>Current: {config.version}</label>
                    </div>
                    <div className="form-item">
                        <label>Lastest: {config.lastest}</label>
                        {
                            config.lastest > config.version ?
                            <a className="btn-update" onClick={onUpdate}>Update</a>:
                            null
                        }
                        
                    </div>
                </form>
                <footer>
                    <button className="btn cancel" onClick={hidePanelHandler}>Back</button>
                </footer>
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        config: state.global
    }
}

export default connect(mapStateToProps, {})(PreferencePanel);