import React, { useState } from 'react';

import './SmbTimerCamCfg.scss';

const IMAGE_SIZE_ENUM = [
    {text: '1920x1080', value: 14},
    {text: '1280x720', value: 11},
    {text: '640x480', value: 8}
]

function SmbTimerCamCfg({}) {
    const [ssid, setSSID] = useState(localStorage.getItem('ssid') || '');
    const [pwd, setPwd] = useState(localStorage.getItem('password') || '');
    const [host, setHost] = useState('');
    const [user, setUser] = useState('');
    const [smbPwd, setSmbPwd] = useState('');
    const [path, setPath] = useState('');
    const [prefix, setPrefix] = useState('img');
    const [imageSize, setImageSize] = useState(14);
    const [interval, setInterval] = useState(5);
    const [pwdVisible, setPwdVisible] = useState(false);
    const [smbPwdVisible, setSmbPwdVisible] = useState(false);
    const togglePwdVisible = () => setPwdVisible(!pwdVisible);
    const toggleSmbPwdVisible = () => setSmbPwdVisible(!smbPwdVisible);
    const saveSSID = ev => {
        setSSID(ev.target.value);
        localStorage.setItem('ssid', ev.target.value);
    }
    const savePwd = ev => {
        setPwd(ev.target.value);
        localStorage.setItem('password', ev.target.value);
    }
    const ImageSizeList = (() => {
        return IMAGE_SIZE_ENUM.map((item, index) => <option key={index} value={item.value}>{item.text}</option>);
    })();

    return (<>
        <table className="wrapper-timercambox">
            <tbody>
                <tr className="wifibox-item">
                    <td className="field-name">WIFI SSID:</td>
                    <td>
                        <input id="smbtimercam__config__ssid" type="text" value={ssid} onChange={saveSSID} />
                    </td>
                </tr>
                <tr className="wifibox-item password">
                    <td className="field-name">WIFI Password:</td>
                    <td>
                        <input id="smbtimercam__config__password" type={!pwdVisible?"password":"text"} value={pwd} onChange={savePwd} />
                        <img
                            className="btn-eye"
                            src={!pwdVisible ? "./assets/images/show.png" : "./assets/images/hide.png"}
                            onClick={togglePwdVisible}
                            alt=""
                        />
                    </td>
                </tr>
                <tr className="wifibox-item">
                    <td className="field-name">Host/IP:</td>
                    <td>
                        <input id="smbtimercam__config__host" placeholder="eg: xxxxxxx.com or 0.0.0.0" type="text" value={host} onChange={ev => setHost(ev.target.value)} />
                    </td>
                </tr>
                <tr className="wifibox-item">
                    <td className="field-name">Username:</td>
                    <td>
                        <input id="smbtimercam__config__user" type="text" value={user} onChange={ev => setUser(ev.target.value)} />
                    </td>
                </tr>
                <tr className="wifibox-item password">
                    <td className="field-name">Password:</td>
                    <td>
                        <input id="smbtimercam__config__smbpassword" type={!smbPwdVisible?"password":"text"} value={smbPwd} onChange={ev => setSmbPwd(ev.target.value)} />
                        <img
                            className="btn-eye"
                            src={!pwdVisible ? "./assets/images/show.png" : "./assets/images/hide.png"}
                            onClick={toggleSmbPwdVisible}
                            alt=""
                        />
                    </td>
                </tr>
                <tr className="wifibox-item">
                    <td className="field-name">FolderPath:</td>
                    <td>
                        <input id="smbtimercam__config__path" type="text" placeholder="eg: examples/demo" value={path} onChange={ev => setPath(ev.target.value)} />
                    </td>
                </tr>
                <tr className="wifibox-item">
                    <td className="field-name">FilePrefix:</td>
                    <td>
                        <input id="smbtimercam__config__prefix" type="text" value={prefix} onChange={ev => setPrefix(ev.target.value)} />
                    </td>
                </tr>
                <tr className="wifibox-item">
                    <td className="field-name">Image Size:</td>
                    <td>
                        <select id="smbtimercam__config__imagesize" value={imageSize} onChange={ev => setImageSize(ev.target.value)}>
                            {ImageSizeList}
                        </select>
                    </td>
                </tr>
                <tr className="wifibox-item">
                    <td className="field-name">Interval:</td>
                    <td>
                        <input id="smbtimercam__config__interval" type="number" value={interval} onChange={ev => setInterval(ev.target.value)} />
                        <span>(senconds)</span>
                    </td>
                </tr>
            </tbody>
        </table>
    </>)
}

export default SmbTimerCamCfg;