import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import cs from 'classnames';
import { sendCommand, CommandTypes } from './ipc/events';
import TopBar from './containers/TopBar';
import Header from './containers/Header';
import Firmware from './containers/Firmware/Firmware';
import DynamicModal from './containers/DynamicModal/DynamicModal';
import { setTheme, setBurnParams, setVersion, setLastestVersion, setLoginState, setOwns } from './actions/global.action';
import { setBaudrate } from './actions/burn.action';

require('./common.scss');

const electron = window.require('electron');
const { ipcRenderer } = electron;

const App = (props) => {
    const { 
        theme,
        setTheme,
        setBurnParams,
        setBaudrate,
        setVersion,
        setLastestVersion,
        setLoginState,
        setOwns
    } = props;

    useEffect(() => {
        sendCommand(CommandTypes.loadAllConfig, null, data => {
            console.log(data);
            setTheme(data['theme']);
            setBurnParams(data['burnParams']);
            setBaudrate(data['baudrate']);
            setVersion(data['version']);
            setLastestVersion(data['lastest']);
            setLoginState({isLogin: data['isLogin'], ssoToken: data['ssoToken']});
        });

        ipcRenderer.on('GET_ALL_HARDWARES', (ev, data) => {
            let all = data.data;
            setOwns(all);
        })
    }, []);
    
    return (
        <div className={cs('main', theme)}>
            <TopBar />
            <Header />
            <Firmware />
            <DynamicModal />
        </div>
    )
}

const mapStateToProps = state => {
    return {
        theme: state.global.theme
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setTheme: theme => dispatch(setTheme({theme})),
        setBaudrate: baudrate => dispatch(setBaudrate({baudrate})),
        setBurnParams: params => dispatch(setBurnParams({...params})),
        setVersion: version => dispatch(setVersion({version})),
        setLastestVersion: lastest => dispatch(setLastestVersion({lastest})),
        setLoginState: loginInfo => dispatch(setLoginState({loginState: loginInfo.isLogin, ssoToken: loginInfo.ssoToken})),
        setOwns: hardwares => dispatch(setOwns({owns: hardwares}))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);