import React from 'react';
import cs from 'classnames';
import { connect } from 'react-redux';
import { closeModal, setAnimation } from '../../actions/modal.action';
import { AnimationState } from '../../constants';

import './DynamicModal.scss';

class DynamicModal extends React.Component {

    constructor(props) {
        super(props);

        this.beforeCloseModal = this.beforeCloseModal.bind(this);
    }

    beforeCloseModal() {
        this.props.setAnimation(AnimationState.FADE_OUT);
    }

    shouldComponentUpdate(newProps) {
        // When open mdoal
        if(this.props.modal.visiable !== newProps.modal.visiable && newProps.modal.visiable) {
            setTimeout(() => {
                this.props.setAnimation(AnimationState.FADE_IN);
            });
        }

        // When close modal, set `opacity` to 0 before set `display` to none
        if(this.props.modal.animation !== newProps.modal.animation &&
            newProps.modal.animation === AnimationState.FADE_OUT) {
                setTimeout(() => {
                    this.props.closeModal();
                }, 250);
            }
            
        return true;
    }

    render() {
        const { visiable, component, animation } = this.props.modal;
        if(!visiable) {
            return null;
        }
        return (
            <div className={cs('full-main', animation)}>
                <div className="mask" onClick={this.beforeCloseModal}></div>
                <div className="modal">
                    { component }
                </div>
            </div>
        )
    }

}

const mapStateToProps = state => {
    return {
        modal: state.modal
    }
}

const mapDispatchToProps = dispatch => {
    return {
        closeModal: _ => dispatch(closeModal()),
        setAnimation: animation => dispatch(setAnimation({animation}))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DynamicModal);