const electron = window.require('electron');
const { ipcRenderer } = electron;

export const CommandTypes = {
    minimize: 'Minimize',
    maximize: 'Maximize',
    quit: 'Quit',
    restart: 'Restart',
    getPorts: 'GetPorts',
    erase: 'Erase',
    burn: 'Burn',
    cancelBurn: 'CancelBurn',
    saveConfig: 'SaveConfig',
    loadConfig: 'loadConfig',
    loadAllConfig: 'LoadAllConfig',
    watchCOM: 'WatchCOM',
    closeCOM: 'CloseCOM',
    getFirmwares: 'GetFirmwares',
    downloadFirmware: 'DownloadFirmware',
    removeFirmware: 'RemoveFirmware',
    writeCOM: 'WriteCOM',
    update: 'Update',
    getSttToken: 'GetSttToken',
    getUIFlowSetting: 'GetUIFlowSetting',
    setUIFlowSetting: 'SetUIFlowSetting',
    killTask: 'KillTask',
    login: 'Login',
    logout: 'Logout',
    bindDevice: 'BindDevice',
    resetDevice: 'ResetDevice'
}

/**
 * Send command with a callback event
 * @param {CommandTypes} cmd Command
 * @param {*} args Arguments
 * @param {Function} callback Callback event
 */
export const sendCommand = (cmd, args, callback) => {
    let eventName = `${cmd}Callback`;
    if(!ipcRenderer.eventNames().includes(eventName)) {
        ipcRenderer.on(eventName, (ev, data) => {
            if(callback) {
                callback(data);
            }
        });
    }
    ipcRenderer.send(cmd, args);
}

export const sendCommandOnce = (cmd, args, callback) => {
    let eventName = `${cmd}Callback`;
    if(!ipcRenderer.eventNames().includes(eventName)) {
        ipcRenderer.once(eventName, (ev, data) => {
            if(callback) {
                callback(data);
            }
        });
    }
    ipcRenderer.send(cmd, args);
}

export const sendCommandWithCustom = (sub, cmd, args, callback) => {
    let eventName = `${cmd}Callback-${sub}`;
    if(!ipcRenderer.eventNames().includes(eventName)) {
        ipcRenderer.on(eventName, (ev, data) => {
            if(callback) {
                callback(data);
            }
        });
    }
    ipcRenderer.send(cmd, args);
}

/**
 * Send command sync handler
 * @param {CommandTypes} cmd Command
 * @param {*} args Arguments
 * @param {Function} callback Callback event
 */
export const sendCommandSync = (cmd, args, callback) => {
    let response = ipcRenderer.sendSync(cmd, args);
    if(callback) {
        callback(response);
    }
}
