import React from 'react';
import { connect } from 'react-redux';
import { sendCommand, CommandTypes, sendCommandOnce } from '../../ipc/events';
import { AnimationState, BaudrateOptions } from '../../constants';
import { HeaderFormSelector } from '../HeaderFormItem';
import SerialLog from '../SerialLog';
import { setLoginState } from '../../actions/global.action';
import { openModal, setAnimation } from '../../actions/modal.action';
import { setBaudrate, setCOM } from '../../actions/burn.action';
import { startStdout, appendStdout } from '../../actions/stdout.action';

import './HeaderForm.scss';

const electron = window.require('electron');
const { ipcRenderer } = electron;

class HeaderForm extends React.Component {

    bindLock = false;

    constructor() {
        super();

        this.state = {
            coms: [],
            ssid: localStorage.getItem('ssid') || '',
            password: localStorage.getItem('password') || '',
            passwordVisible: false
        }

        this.changeCOMHandler = this.changeCOMHandler.bind(this);
        this.changeBaudrateHandler = this.changeBaudrateHandler.bind(this);
        this.refreshCOMs = this.refreshCOMs.bind(this);
        this.erase = this.erase.bind(this);
        this.watchCOM = this.watchCOM.bind(this);
        this.updateSSID = this.updateSSID.bind(this);
        this.updatePassword = this.updatePassword.bind(this);
        this.togglePasswordVisible = this.togglePasswordVisible.bind(this);
        this.bindDevice = this.bindDevice.bind(this);
    }

    componentWillMount() {
        this._getPorts();
    }

    _getPorts() {
        sendCommand(CommandTypes.getPorts, null, data => {
            this.setState({
                coms: data.map(port => port.path)
            }, () => {
                if(data.length > 0 && this.props.com === '') {
                    this.props.setCOM(this.state.coms[0]);
                }
            });
        });
    }

    refreshCOMs(ev) {
        this._getPorts();
        if(this.props.com !== ev.target.value) {
            this.props.setCOM(ev.target.value);
        }
    }

    changeCOMHandler(ev) {
        this.props.setCOM(ev.target.value);
    }

    changeBaudrateHandler(ev) {
        this.props.setBaudrate(ev.target.value);
        sendCommand(CommandTypes.saveConfig, { key: 'baudrate', value: ev.target.value });
    }

    erase(ev) {
        ev.preventDefault();

        let closeModalHandler = this.props.closeModal;
        const footerButtons = (function() {
            return(
                <>
                    <div className="footer-btn">
                        <button onClick={() => {
                            ipcRenderer.send(CommandTypes.killTask);
                            closeModalHandler();
                        }}>Close</button>
                    </div>
                </>
            )
        })();

        this.props.openModal(
            <SerialLog footer={footerButtons}/>
        );

        this.props.startStdoutMessage('=================\nErase now\n=================\n');
        sendCommand(CommandTypes.erase, { com: this.props.com }, data => {
            this.props.appendStdoutMessage(data);
        });
    }

    watchCOM(ev) {
        ev.preventDefault();

        let _this = this;
        let closeModalHandler = this.props.closeModal;
        const footerButtons = (function() {

            const sendHandler = () => {
                let content = document.getElementById('_sendbox').value;
                sendCommand(CommandTypes.writeCOM, {
                    com: _this.props.com,
                    data: content
                }, null);
            }

            const resetHandler = () => {
                sendCommand(CommandTypes.resetDevice, {
                    com: _this.props.com
                }, null);
            }

            const keydownHandler = (ev) => {
                if(ev.charCode === 13 && ev.ctrlKey) {
                    sendHandler();
                }
            }

            return(
                <>
                    <div className="footer-btn">
                        <textarea id="_sendbox" className="sendbox" type="text" onKeyPress={keydownHandler}></textarea>
                        <button className="reset" onClick={resetHandler}>Reset</button>
                        <button className="send" onClick={sendHandler}>Send</button>
                        <button onClick={() => {
                            if(ipcRenderer.eventNames().includes(`${CommandTypes.watchCOM}Callback`)) {
                                sendCommand(CommandTypes.closeCOM, null);
                            }
                            closeModalHandler();
                        }}>Close</button>
                    </div>
                </>
            )
        })();

        this.props.openModal(<SerialLog footer={footerButtons} com={this.props.com} />);

        this.props.startStdoutMessage(`=================\n${this.props.com}\n=================\n`);
        sendCommand(CommandTypes.watchCOM, { com: this.props.com }, data => {
            this.props.appendStdoutMessage(data);
        });
    }

    updateSSID(ev) {
        this.setState({
            ssid: ev.target.value
        });
        localStorage.setItem('ssid', ev.target.value);
    }

    updatePassword(ev) {
        this.setState({
            password: ev.target.value
        });
        localStorage.setItem('password', ev.target.value);
    }

    togglePasswordVisible() {
        this.setState({
            passwordVisible: !this.state.passwordVisible
        })
    }

    bindDevice(ev) {
        ev.preventDefault();

        window.swal({
            title: '',
            text: 'Adding device, wait a minute...',
            icon: 'warning',
            closeOnClickOutside: false,
            closeOnEsc: false,
            buttons: false
        })

        sendCommandOnce(CommandTypes.bindDevice, {
            com: this.props.com
        }, ret => {
            if(ret === -1) {
                // 
                this.props.setLoginState(false);
                window.swal({
                    title: '',
                    text: 'The login status has expired, please log in again',
                    icon: 'warning'
                })
                return;
            }
            else if(ret === false) {
                window.swal({
                    title: '',
                    text: 'Invalid device',
                    icon: 'error'
                })
            }
            else if(ret === true) {
                window.swal({
                    title: '',
                    text: 'Device has been added in your account.',
                    icon: 'success'
                })
            }
            else if(ret === -1000) {
                window.swal({
                    title: '',
                    text: 'Network occured errors',
                    icon: 'error'
                })
            }
            else if(ret === 400) {
                window.swal({
                    className: 'binding-tip-modal',
                    title: '',
                    text: 'This device has been bound, try again after unbinding.',
                    icon: 'error'
                })
            }
            else {
                window.swal({
                    title: '',
                    text: 'Network occured errors',
                    icon: 'error'
                })
            }
        });
    }

    render() {
        const { com, baudrate } = this.props;
        
        return (
            <form className="header-form">
                <HeaderFormSelector
                    icon='icon-usb'
                    field='COM'
                    value={com}
                    options={this.state.coms}
                    onChange={this.changeCOMHandler.bind(this)}
                    onClick={this.refreshCOMs.bind(this)}
                />
                <HeaderFormSelector
                    icon=''
                    field='Baudrate'
                    value={baudrate}
                    options={BaudrateOptions}
                    onChange={this.changeBaudrateHandler}
                />
                <button className="btn erase" onClick={this.erase}>
                    <i className="iconfont icon-erase"></i>
                    <span>Erase</span>
                </button>
                <button className="btn open" onClick={this.watchCOM}>
                    <i className="iconfont icon-usb"></i>
                    <span>COM Monitor</span>
                </button>
                {
                    this.props.global.loginState
                    ? <button className="btn bind" onClick={this.bindDevice}>
                        <i className="iconfont icon-bind"></i>
                        <span>Bind with account</span>
                    </button>
                    : null
                }
            </form>
        )
    }
}

const mapStateToProps = state => {
    return {
        com: state.burn.com,
        global: state.global,
        baudrate: state.burn.baudrate
    }
}

const mapDispatchToProps = dispatch => {
    return {
        openModal: component => dispatch(openModal({component})),
        closeModal: _ => dispatch(setAnimation({animation: AnimationState.FADE_OUT})),
        setBaudrate: baudrate => dispatch(setBaudrate({baudrate})),
        setCOM: com => dispatch(setCOM({com})),
        startStdoutMessage: message => dispatch(startStdout({message})),
        appendStdoutMessage: message => dispatch(appendStdout({message})),
        setLoginState: loginState => dispatch(setLoginState({loginState}))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(HeaderForm);