import React from 'react';
import cs from 'classnames';

function HeaderFormSelector(props) {
    let { icon, field, value, options, onChange, onClick } = props;
    return (
        <li className="header-form-item">
            <i className={cs('iconfont', icon)}></i>
            <span>{field}:</span>
            <select value={value} onChange={onChange} onClick={onClick}>
                {
                    options.map(opt => {
                        return (<option key={opt} value={opt}>{opt}</option>)
                    })
                }
            </select>
        </li>
    )
}

export default HeaderFormSelector;