export const StdoutActionTypes = {
    START_STDOUT: 'START_STDOUT',
    APPEND_STDOUT: 'APPEND_STDOUT'
}

export function startStdout(payload) {
    return {
        type: StdoutActionTypes.START_STDOUT,
        payload
    }
}

export function appendStdout(payload) {
    return {
        type: StdoutActionTypes.APPEND_STDOUT,
        payload
    }
}