import React from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import cs from 'classnames';
import { AnimationState } from '../../constants';
import { CommandTypes, sendCommand, sendCommandSync, sendCommandOnce } from '../../ipc/events';
import { openModal, setAnimation } from '../../actions/modal.action';
import { setTheme, setBurnParams, setLoginState } from '../../actions/global.action';
import PreferencePanel from '../../components/PreferencePanel';
import LoginForm from '../../components/LoginForm/LoginForm';

import './TopBar.scss';

const electron = window.require('electron');
const { ipcRenderer, shell } = electron;

class TopBar extends React.Component {

    constructor() {
        super();

        this.maximize = this.maximize.bind(this);
        this.minimize = this.minimize.bind(this);
        this.quit = this.quit.bind(this);
        this.update = this.update.bind(this);
        this.openPreferencePanel = this.openPreferencePanel.bind(this);
        this.changeSelectorHandler = this.changeSelectorHandler.bind(this);
        this.openLoginForm = this.openLoginForm.bind(this);
        this.handleLogout = this.handleLogout.bind(this);
        this.handleGotoDeviceManagement = this.handleGotoDeviceManagement.bind(this);

        window.test = this.update.bind(this);
    }

    openPreferencePanel() {
        if(!this.props.modal.visiable) {
            const closePanelHandler = this.props.hideModal;
            this.props.openModal(
                <PreferencePanel
                    onChange={this.changeSelectorHandler}
                    onClose={closePanelHandler}
                    onUpdate={this.update}
                />
            );
        }
    }

    changeSelectorHandler(key, value) {
        if(key === 'theme') {
            this.props.setTheme(value);
            sendCommand(CommandTypes.saveConfig, { key, value });
        } else {
            this.props.setBurnParams(key, value);
            sendCommand(CommandTypes.saveConfig, { key: 'burnParams', value: { [key]: value } });
        }
    }

    maximize() {
        sendCommand(CommandTypes.maximize, null, null);
    }

    minimize() {
        sendCommand(CommandTypes.minimize, null, null);
    }

    quit() {
        sendCommand(CommandTypes.quit, null, null);
    }

    update() {
        window.swal({
            title: 'Tips',
            text: 'Do you sure to update?',
            icon: 'warning',
            buttons: {
                confirm: {
                    text: 'Yes',
                    closeModal: false,
                    value: true
                },
                cancel: {
                    text: 'No',
                    closeModal: false,
                    value: false,
                    visible: true
                }
            },
            closeOnClickOutside: false,
            closeOnEsc: false
        })
        .then(result => {
            if(!result) {
                window.swal.getState().actions.cancel.closeModal = true;
                window.swal.stopLoading();
                window.swal.close();
                return;
            }
            let title = document.getElementsByClassName('swal-title')[0];
            title.style.display = 'none';
            sendCommand(CommandTypes.update, { lastest: this.props.global.lastest }, data => {
                let text = document.getElementsByClassName('swal-text')[0];
                if(data === false) {
                    text.innerText = 'Download update failed.';
                    window.swal.getState().actions.confirm.closeModal = true;
                    window.swal.getState().actions.cancel.closeModal = true;
                    window.swal.stopLoading();
                    return;
                }
                if(data === true) {
                    text.innerText = 'Update successfully. Restart takes effect';
                    window.swal.getState().actions.confirm.closeModal = true;
                    window.swal.getState().actions.cancel.closeModal = true;
                    window.swal.stopLoading();

                    let confirmBtn = document.querySelectorAll('.swal-button.swal-button--confirm');
                    if(confirmBtn.length > 0) {
                        confirmBtn[0].addEventListener('click', ev => {
                            sendCommandSync(CommandTypes.restart, null);
                        });
                    }
                    return;
                }
                text.innerText = `Update ... (${data * 100}%)`;
            });
        })
    }

    handleLogin(email, password, errHandler) {
        sendCommandOnce(CommandTypes.login, {
            email,
            password
        }, ret => {
            if(ret) {
                localStorage.setItem('m5sso_email', email);
                localStorage.setItem('m5sso_password', password);
                window.swal.close();

                window.swal({
                    text: `Welcome ${email}`,
                    icon: 'success',
                });

                this.props.setLoginState({isLogin: true, ssoToken: ret});
            } else {
                errHandler()
            }
        })
    }

    handleLogout() {
        window.swal({
            title: 'Logout',
            text: 'Do you sure to logout?',
            icon: 'warning',
            buttons: {
                confirm: {
                    text: 'Yes',
                    value: true
                },
                cancel: {
                    text: 'No',
                    value: false,
                    visible: true
                }
            }
        })
        .then(ret => {
            if(ret) {
                sendCommandOnce(CommandTypes.logout, null, () => {
                    this.props.setLoginState({isLogin: false, ssoToken: ''});
                });
            }
        })
    }

    handleGotoRegister() {
        shell.openExternal('https://forum.m5stack.com/register');
    }

    handleGotoReset() {
        shell.openExternal('https://forum.m5stack.com/reset');
    }

    handleGotoDeviceManagement() {
        shell.openExternal('https://devm.m5stack.com/' + `?sid=${this.props.global.ssoToken}`);
    }

    openLoginForm() {
        let container = document.createElement('div');
        ReactDOM.render(<LoginForm
            onLogin={this.handleLogin.bind(this)}
            gotoRegister={this.handleGotoRegister.bind(this)}
            gotoReset={this.handleGotoReset.bind(this)}
        />, container);
        window.swal({
            className: 'wifi-modal',
            content: container,
            closeOnEsc: false,
            closeOnClickOutside: false,
            buttons: {}
        })
    }

    checkVersion() {
        const { version, lastest } = this.props.global;
        if(lastest > version ) {
            return true;
        }
        return false;
    }

    render() {
        let loginedEmail = localStorage.getItem('m5sso_email') || '';
        let needUpdate = this.checkVersion();
        return (
            <div className="app-top-bar">
                <div className="logo">
                    <img className="topbar-logo" src="assets/images/logo.png" />
                    <span>M5Burner</span>
                </div>
                <div className="control-box">
                    {
                        this.props.global.loginState
                        ? (
                            <>
                                <button className="btn-control btn-user">
                                    <i className="iconfont icon-user"></i>
                                    <span>{loginedEmail}</span>

                                    <div className="menu">
                                        <p onClick={this.handleGotoDeviceManagement}>
                                            <i className="iconfont icon-device"></i>
                                            <span>Device Management</span>
                                        </p>
                                    </div>
                                </button>
                                <button className="btn-control btn-user" onClick={this.handleLogout}>
                                    <i className="iconfont icon-log-out"></i>
                                </button>
                            </>
                        )
                        : <button className="btn-control btn-user" onClick={this.openLoginForm}>
                            <i className="iconfont icon-user"></i>
                        </button>
                    }
                    
                    <button
                        className={cs('btn-control', 'btn-setting', needUpdate ? 'notice' : '')}
                        onClick={this.openPreferencePanel}
                    >
                        <i className="iconfont icon-setting"></i>
                    </button>
                    <button className="btn-control btn-min" onClick={this.minimize}>
                        <i className="iconfont icon-zuixiaohua"></i>
                    </button>
                    <button className="btn-control btn-max" onClick={this.maximize}>
                        <i className="iconfont icon-zuidahua"></i>
                    </button>
                    <button className="btn-control btn-close" onClick={this.quit}>
                        <i className="iconfont icon-guanbi"></i>
                    </button>
                </div>
            </div>
        )
    }

}

const mapStateToProps = state => {
    return {
        modal: state.modal,
        global: state.global
    }
}

const mapDispatchToProps = dispatch => {
    return {
        openModal: component => dispatch(openModal({ component })),
        hideModal: _ => dispatch(setAnimation({animation: AnimationState.FADE_OUT})),
        setTheme: theme => dispatch(setTheme({ theme })),
        setBurnParams: (key, value) => dispatch(setBurnParams({[key]: value})),
        setLoginState: loginInfo => dispatch(setLoginState({loginState: loginInfo.isLogin, ssoToken: loginInfo.ssoToken}))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TopBar);