import React from 'react';
import { connect } from 'react-redux';
import cs from 'classnames';
import { sendCommand, CommandTypes, sendCommandOnce } from '../../ipc/events';
import { getFirmware, selectDeviceType, updateDownloadStatus, updateFirmwares, searchFirmware } from '../../actions/firmware.action';
import DeviceList from '../../components/DeviceList';
import Searchbox from '../../components/Searchbox';
import FirmwareList from '../../components/FirmwareList';
import Loading from '../../components/Loading/Loading';
import { DownloadStatus } from '../../constants';

import './Firmware.scss';
import { setBaudrate } from '../../actions/burn.action';

const electron = window.require('electron');
const ipcRenderer = electron.ipcRenderer;

class Firmware extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true
        }

        this.downloadHanlder = this.downloadHanlder.bind(this);
        this.removeHanlder = this.removeHanlder.bind(this);
        this._updateFirmwares = this._updateFirmwares.bind(this);
        this.setCurrentDevice = this.setCurrentDevice.bind(this);
        this.inputChangeHandler = this.inputChangeHandler.bind(this);
    }

    componentWillMount() {
        this._getFirmwares();
    }

    _getFirmwares() {
        this.setState({
            loading: true
        });
        sendCommandOnce(CommandTypes.getFirmwares, false, data => {
            console.log(data);
            this.setState({
                loading: false
            });
            if(data === null) return;
            this.props.getFirmwares(data);
        });
    }

    _updateFirmwares(local) {
        sendCommandOnce(CommandTypes.getFirmwares, local, data => {
            if(data === null) return;
            this.props.updateFirmwares(data);
        });
    }

    removeHanlder(payload) {
        let _updateFirmwares = this._updateFirmwares;
        window.swal({
            title: 'Tips',
            text: 'Do you sure to delete this firmware?',
            icon: 'warning',
            buttons: {
                ok: 'Yes',
                cancel: 'No'
            }
        })
        .then(r => {
            if(r == 'ok') {
                let { name, version } = payload;
                let selectedType = this.props.firmware.selected;
                let data = {
                    firmware: name,
                    version: version,
                    type: selectedType
                }
                sendCommand(CommandTypes.removeFirmware, data, data => {
                    if(data) {
                        _updateFirmwares(true);
                    }
                });
            }
        });
    }

    downloadHanlder(payload) {
        let _updateFirmwares = this._updateFirmwares;
        let { name, version, type } = payload;
        let callbackSubscribe = `${CommandTypes.downloadFirmware}Callback-${name}-${type}-${version}`;
        let updateFirmwareDownloadStatus = this.props.updateFirmwareDownloadStatus;
        ipcRenderer.send(CommandTypes.downloadFirmware, {
            firmware: name,
            version: version,
            type: type
        });
        if(ipcRenderer.eventNames().includes(callbackSubscribe)) {
            return;
        }
        ipcRenderer.on(callbackSubscribe, function(ev, progress) {
            // Download status
            // return null as duplicate download
            // return 'end' as failed
            // return 0-1 is the progress of download
            if(progress === null) {
                return;
            }
            if(progress === 'end') {
                // When download error
                updateFirmwareDownloadStatus({
                    name,
                    version,
                    type,
                    downloadStatus: DownloadStatus.ERROR,
                    progress: 0
                });
                ipcRenderer.off(callbackSubscribe, this.listeners(callbackSubscribe)[0]);
                return;
            }
            if(progress == 1) {
                // When download finish
                updateFirmwareDownloadStatus({
                    name,
                    version,
                    type,
                    downloadStatus: DownloadStatus.END,
                    progress: 1
                });
                ipcRenderer.off(callbackSubscribe, this.listeners(callbackSubscribe)[0]);
                _updateFirmwares(true);
                return;
            }
            updateFirmwareDownloadStatus({
                name,
                version,
                type,
                downloadStatus: DownloadStatus.DOWNLOADING,
                progress
            });
        });
    }

    setCurrentDevice(type) {
        let baudrate = '1500000';
        switch(type) {
            case 'core':
                baudrate = '1500000';
                break;
            case 'stickc':
                baudrate = '750000';
                break;
            case 'atom':
                baudrate = '750000';
                break;
            case 'stickv':
                baudrate = '1500000';
                break;
            case 'timercam':
                baudrate = '1500000';
                break;
            case 'coreink':
                baudrate = '1500000';
                break;
            default:
                break;
        }
        this.props.setSelectedDeviceType(type);
        this.props.setBaudrate(baudrate);
        sendCommand(CommandTypes.saveConfig, { key: 'baudrate', value: baudrate });
    }

    inputChangeHandler(ev) {
        this.props.searchFirmware(ev.target.value);
    }

    render() {
        const { loading } = this.state;
        const { list, selected } = this.props.firmware;
        const { owns } = this.props.global;
        return (
            <div className="container-firmware">
                <DeviceList owns={owns} selected={selected} setSelected={this.setCurrentDevice} />
                <div className="wrapper-firmware-list">
                    {
                        loading ?
                        <Loading /> :
                        <>
                            <Searchbox onChange={this.inputChangeHandler} />
                            <div className={cs('list', selected === 'core' ? 'active' : '')}>
                                <FirmwareList
                                    device={'core'}
                                    firmwareList={list['core']}
                                    downloadHanlder={this.downloadHanlder}
                                    removeHandler={this.removeHanlder}
                                />
                            </div>
                            <div className={cs('list', selected === 'core2' ? 'active' : '')}>
                                <FirmwareList
                                    device={'core2'}
                                    firmwareList={list['core2']}
                                    downloadHanlder={this.downloadHanlder}
                                    removeHandler={this.removeHanlder}
                                />
                            </div>
                            <div className={cs('list', selected === 'stickc' ? 'active' : '')}>
                                <FirmwareList
                                    device={'stickc'}
                                    firmwareList={list['stickc']}
                                    downloadHanlder={this.downloadHanlder}
                                    removeHandler={this.removeHanlder}
                                />
                            </div>
                            <div className={cs('list', selected === 'atom' ? 'active' : '')}>
                                <FirmwareList
                                    device={'atom'}
                                    firmwareList={list['atom']}
                                    downloadHanlder={this.downloadHanlder}
                                    removeHandler={this.removeHanlder}
                                />
                            </div>
                            <div className={cs('list', selected === 'stickv' ? 'active' : '')}>
                                <FirmwareList
                                    device={'stickv'}
                                    firmwareList={list['stickv']}
                                    downloadHanlder={this.downloadHanlder}
                                    removeHandler={this.removeHanlder}
                                />
                            </div>
                            <div className={cs('list', selected === 'timercam' ? 'active' : '')}>
                                <FirmwareList
                                    device={'timercam'}
                                    firmwareList={list['timercam']}
                                    downloadHanlder={this.downloadHanlder}
                                    removeHandler={this.removeHanlder}
                                />
                            </div>
                            <div className={cs('list', selected === 'coreink' ? 'active' : '')}>
                                <FirmwareList
                                    device={'coreink'}
                                    firmwareList={list['coreink']}
                                    downloadHanlder={this.downloadHanlder}
                                    removeHandler={this.removeHanlder}
                                />
                            </div>
                            <div className={cs('list', selected === 'paper' ? 'active' : '')}>
                                <FirmwareList
                                    device={'paper'}
                                    firmwareList={list['paper']}
                                    downloadHanlder={this.downloadHanlder}
                                    removeHandler={this.removeHanlder}
                                />
                            </div>
                            <div className={cs('list', selected === 'stamp' ? 'active' : '')}>
                                <FirmwareList
                                    device={'stamp'}
                                    firmwareList={list['stamp']}
                                    downloadHanlder={this.downloadHanlder}
                                    removeHandler={this.removeHanlder}
                                />
                            </div>
                        </>
                    }
                </div>
            </div>
        )
    }

}

const mapStateToProps = state => {
    return {
        firmware: state.firmware,
        global: state.global
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getFirmwares: firmwares => dispatch(getFirmware(firmwares)),
        updateFirmwares: firmwares => dispatch(updateFirmwares(firmwares)),
        setSelectedDeviceType: type => dispatch(selectDeviceType(type)),
        updateFirmwareDownloadStatus: payload => dispatch(updateDownloadStatus(payload)),
        setBaudrate: baudrate => dispatch(setBaudrate({baudrate})),
        searchFirmware: keyword => dispatch(searchFirmware(keyword))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Firmware);