import React, { useState } from 'react';

import './TimerCamCfg.scss';

const IMAGE_SIZE_ENUM = [
    '96x96',
    '160x120',
    '176x144',
    '240x176',
    '240x240',
    '320x240',
    '400x296',
    '480x320',
    '640x480',
    '800x600',
    '1024x768',
    '1280x720',
    '1280x1024',
    '1600x1200',
];

export default props => {
    const [ssid, setSSID] = useState(localStorage.getItem('ssid') || '');
    const [pwd, setPwd] = useState(localStorage.getItem('password') || '');
    const [accessKey, setAccessKey] = useState('');
    const [accessKeySecret, setAccessKeySecret] = useState('');
    const [serverAddr, setServerAddr] = useState('');
    const [bucket, setBucket] = useState('');
    const [wakeTime, setWakeTime] = useState(30);
    const [imageSize, setImageSize] = useState(5);
    const [pwdVisible, setPwdVisible] = useState(false);
    const togglePwdVisible = () => setPwdVisible(!pwdVisible);
    const saveSSID = ev => {
        setSSID(ev.target.value);
        localStorage.setItem('ssid', ev.target.value);
    }
    const savePwd = ev => {
        setPwd(ev.target.value);
        localStorage.setItem('password', ev.target.value);
    }
    const handleBlur = ev => {
        if(ev.target.value === '') {
            setWakeTime(30);
        }
        if(ev.target.value > 15240) {
            setWakeTime(15240);
        }
        if(ev.target.value < 30) {
            setWakeTime(30);
        }
    }
    const ImageSizeList = (() => {
        return IMAGE_SIZE_ENUM.map((size, index) => <option key={index} value={index}>{size}</option>);
    })();

    return (
        <>
            <table className="wrapper-timercambox">
                <tbody>
                    <tr className="wifibox-item">
                        <td className="field-name">SSID:</td>
                        <td>
                            <input id="timercam__config__ssid" type="text" value={ssid} onChange={saveSSID} />
                        </td>
                    </tr>
                    <tr className="wifibox-item password">
                        <td className="field-name">Password:</td>
                        <td>
                            <input id="timercam__config__password" type={!pwdVisible?"password":"text"} value={pwd} onChange={savePwd} />
                            <img
                                className="btn-eye"
                                src={!pwdVisible ? "./assets/images/show.png" : "./assets/images/hide.png"}
                                onClick={togglePwdVisible}
                                alt=""
                            />
                        </td>
                    </tr>
                    <tr className="wifibox-item">
                        <td className="field-name">Wake Time:</td>
                        <td>
                            <input
                                id="timercam__config__waketime"
                                type="number"
                                value={wakeTime}
                                onChange={ev => setWakeTime(ev.target.value)}
                                onBlur={handleBlur}
                            />
                            <span>sec (30 ~ 15240)</span>
                        </td>
                    </tr>
                    <tr className="wifibox-item">
                        <td className="field-name">Image Size:</td>
                        <td>
                            <select id="timercam__config__imagesize" value={imageSize} onChange={ev => setImageSize(ev.target.value)}>
                                {ImageSizeList}
                            </select>
                        </td>
                    </tr>

                    {
                        props.firmware == 'TimerCamAliyun'?
                        <>
                        <tr className="wifibox-item">
                            <td></td>
                            <td style={{padding:0}}>
                                <span style={{'fontSize':12,color:'#989898'}}>
                                    The following options refer to
                                    <a style={{marginLeft:4,cursor:'pointer',textDecoration:'underline'}}
                                        onClick={() => props.gotoRefer('https://help.aliyun.com/document_detail/53045.html')}>Aliyun OSS Document</a>
                                </span>
                            </td>
                        </tr>
                        <tr className="wifibox-item">
                            <td className="field-name">Access Key:</td>
                            <td>
                                <input
                                    id="timercam__config__access_key"
                                    type="text"
                                    value={accessKey}
                                    onChange={ev => setAccessKey(ev.target.value)}
                                />
                            </td>
                        </tr>
                        
                        <tr className="wifibox-item">
                            <td className="field-name">Access Key Secret:</td>
                            <td>
                                <input
                                    id="timercam__config__access_key_secret"
                                    type="text"
                                    value={accessKeySecret}
                                    onChange={ev => setAccessKeySecret(ev.target.value)}
                                />
                            </td>
                        </tr>
                        
                        <tr className="wifibox-item">
                            <td className="field-name">Server Address:</td>
                            <td>
                                <input
                                    id="timercam__config__server_address"
                                    type="text"
                                    value={serverAddr}
                                    onChange={ev => setServerAddr(ev.target.value)}
                                    placeholder="eg: oss-cn-shenzhen.aliyuncs.com"
                                />
                            </td>
                        </tr>
                        
                        <tr className="wifibox-item">
                            <td className="field-name">Bucket:</td>
                            <td>
                                <input
                                    id="timercam__config__bucket"
                                    type="text"
                                    value={bucket}
                                    onChange={ev => setBucket(ev.target.value)}
                                    placeholder="eg: m5stack-timer-cam"
                                />
                            </td>
                        </tr></>:null
                    }
                    
                </tbody>
            </table>
        </>
    )
}
